  .main_wrapper_post{
    display: flex;
    margin-top: 15px;
    width: 90%;
    min-height: 100px;
 text-align: left;
 background-color:#91CB40;
 font-size: large;
 align-items: center;
 padding: 5px;
      }

.main_photo {
  width: 80px;
  height: 80px;
  display: flex;
  background-color: gray;
  position: relative;
  justify-content: center;
  align-items: center;
  overflow: hidden;
  margin-left: 10px;
  margin-right: 15px;
  border-radius: 200px 200px 200px 200px;
  -webkit-border-radius: 200px 200px 200px 200px;
  -moz-border-radius: 200px 200px 200px 200px;
}
.main_wrapper_post .main_photo > img{
      width: 120%;
      height: 110%;
    }
.main_content {
  display: flex;
  flex-direction: column;
  width: 80%;
  height: 100%;
}
.main_content > p {
  height: 80%;
  min-height: 60px;
  border: #ffff 1px solid;
border-radius: 5px;
}
