.profileinfo {
  display: flex;
  background-color: #40CBBF;
  justify-content: space-between;
  border: 7px outset #ff5443;
  width: 60%;
}

.hidden {
  visibility: hidden;
}
.hidden.not {
  visibility: visible;
}

    .icon_back{
      display: flex;
      position: relative;
      width: 16%;
      margin: 0px;
      flex-grow: 2;
      flex-wrap: wrap;
      padding-bottom: 30px;
    }
    .icon_back > img {
      width: 100%;
      height: 200px;
      position: relative;

    }
    .descr {
         width: 82%;
         height: 100%;
         margin: 1px;
         flex-grow: 3;
        
    }
    .h1 {
      margin: 0px 0px 0px 15px
    }
    .info {
      margin: 20px 0px 5px 15px;
    }
    .p {
      margin: 15px 0px 0px 3px;
    }
    .contactIconsDiv{
      align-items: baseline;
      margin: 0px;
      height: 30px;
      width: 65%; 
    }
  
   
    .custom_upload_file {
      display: inline-block;
      padding: 6px 12px;
      cursor: pointer;
      transition-duration: 0.3s;
      margin: 0 auto;
      width: 90%;
      height: 50px;
      border-radius: 5px 5px 5px 5px;
    }
    .custom_upload_file {
      display: flex;
      justify-content: space-around;
    }
    .custom_upload_file {
      border:3px solid #208ea7b4;
    }
    .custom_upload_file div {
      color: black;
      margin: 0;
      padding: 0;
      transition-duration: 0.1s;
    }
    input[type="file"] {
      display: none;
  }
  .custom_upload_file .icon {
    color: black;
    transition-duration: 0.3s;
  }
  .custom_upload_file:hover{
    background-color: #208ea7b4;
  }
  .custom_upload_file:hover .icon {
    color: white;
  }
  
  @media (max-width: 1800px) {
    .custom_upload_file {
      flex-direction: column;
      align-items: center;
    }
  }